import { NextRequest, NextResponse } from 'next/server';
import {
  getEndpoint,
  updateEndpoint,
  deleteEndpoint,
  getPaymentsForEndpoint,
  getUsageStats,
} from '@/db/metered-endpoints';
import { requireAuth } from '@/lib/auth';
import { updateEndpointSchema, endpointIdSchema } from '@/lib/schemas';
import { validateRequestBody, validateUrlParam, createValidationErrorResponse } from '@/lib/validate-request';
import { validateEndpointId } from '@/lib/input-validator';

/**
 * GET /api/v1/endpoints/[endpointId]
 * 
 * Get endpoint details, optionally with stats
 * Requires: Authentication (read-only role minimum)
 */
export async function GET(
  request: NextRequest,
  { params }: { params: Promise<{ endpointId: string }> }
) {
  const { endpointId } = await params; // Next.js 15+ requires awaiting params
  
  try {
    // Require authentication
    const authResult = await requireAuth(request, 'read-only');
    if ('response' in authResult) {
      return authResult.response;
    }

    // Validate endpoint ID parameter
    const endpointIdValidation = validateEndpointId(endpointId);
    if (!endpointIdValidation.valid) {
      return NextResponse.json(
        {
          error: 'Invalid endpoint ID',
          code: 'VALIDATION_ERROR',
          details: endpointIdValidation.errors,
        },
        { status: 400 }
      );
    }

    const sanitizedEndpointId = endpointIdValidation.sanitized || endpointId;
    const { searchParams } = new URL(request.url);
    const includeStats = searchParams.get('includeStats') === 'true';

    const endpoint = await getEndpoint(endpointId);

    if (!endpoint) {
      return NextResponse.json(
        {
          error: 'Endpoint not found',
          code: 'NOT_FOUND',
        },
        { status: 404 }
      );
    }

    const response: any = {
      success: true,
      endpoint,
    };

    if (includeStats) {
      // Get recent payments
      const payments = await getPaymentsForEndpoint(endpointId, 10);

      // Get usage stats (last 30 days)
      const thirtyDaysAgo = new Date();
      thirtyDaysAgo.setDate(thirtyDaysAgo.getDate() - 30);
      const stats = await getUsageStats(
        endpointId,
        thirtyDaysAgo.toISOString()
      );

      response.stats = {
        ...stats,
        recentPayments: payments,
      };
    }

    return NextResponse.json(response);
  } catch (error) {
    console.error('[endpoints] Get error:', error);
    return NextResponse.json(
      {
        error: 'Failed to get endpoint',
        code: 'INTERNAL_ERROR',
        details: error instanceof Error ? error.message : 'Unknown error',
      },
      { status: 500 }
    );
  }
}

/**
 * PATCH /api/v1/endpoints/[endpointId]
 * 
 * Update an endpoint
 * Requires: Authentication (user role minimum)
 */
export async function PATCH(
  request: NextRequest,
  { params }: { params: Promise<{ endpointId: string }> }
) {
  const { endpointId } = await params; // Next.js 15+ requires awaiting params
  
  try {
    // Require authentication
    const authResult = await requireAuth(request, 'user');
    if ('response' in authResult) {
      return authResult.response;
    }

    // Validate endpoint ID parameter
    const endpointIdValidation = validateEndpointId(endpointId);
    if (!endpointIdValidation.valid) {
      return NextResponse.json(
        {
          error: 'Invalid endpoint ID',
          code: 'VALIDATION_ERROR',
          details: endpointIdValidation.errors,
        },
        { status: 400 }
      );
    }

    const sanitizedEndpointId = endpointIdValidation.sanitized || endpointId;

    // Validate request body with Zod schema
    const validation = await validateRequestBody(request, updateEndpointSchema);
    if (!validation.valid) {
      return createValidationErrorResponse(validation);
    }

    const body = validation.data;

    // Additional validation for recipient address if provided
    const rawBody = await request.json();
    if (rawBody.recipientAddress) {
      const ethAddressRegex = /^0x[a-fA-F0-9]{40}$/;
      if (!ethAddressRegex.test(rawBody.recipientAddress)) {
        return NextResponse.json(
          {
            error: 'Invalid recipient address',
            code: 'VALIDATION_ERROR',
            details: [{ field: 'recipientAddress', message: 'Must be a valid Ethereum address (0x followed by 40 hex characters)', code: 'invalid_format' }],
          },
          { status: 400 }
        );
      }

      // Validate that recipient is not the USDC contract (blacklisted)
      const USDC_CONTRACT = '0x833589fCD6eDb6E08f4c7C32D4f71b54bdA02913';
      if (rawBody.recipientAddress.toLowerCase() === USDC_CONTRACT.toLowerCase()) {
        return NextResponse.json(
          {
            error: 'Invalid recipient address',
            code: 'VALIDATION_ERROR',
            details: [{ field: 'recipientAddress', message: 'Cannot be the USDC contract address (blacklisted). Use your wallet address instead.', code: 'blacklisted' }],
          },
          { status: 400 }
        );
      }
      // Add recipientAddress to update body
      (body as any).recipientAddress = rawBody.recipientAddress;
    }

    const updated = await updateEndpoint(endpointId, body);

    if (!updated) {
      return NextResponse.json(
        {
          error: 'Endpoint not found',
          code: 'NOT_FOUND',
        },
        { status: 404 }
      );
    }

    return NextResponse.json({
      success: true,
      endpoint: updated,
    });
  } catch (error) {
    console.error('[endpoints] Update error:', error);
    return NextResponse.json(
      {
        error: 'Failed to update endpoint',
        code: 'INTERNAL_ERROR',
        details: error instanceof Error ? error.message : 'Unknown error',
      },
      { status: 500 }
    );
  }
}

/**
 * DELETE /api/v1/endpoints/[endpointId]
 * 
 * Delete (soft delete) an endpoint
 * Requires: Authentication (user role minimum)
 */
export async function DELETE(
  request: NextRequest,
  { params }: { params: Promise<{ endpointId: string }> }
) {
  const { endpointId } = await params; // Next.js 15+ requires awaiting params
  
  try {
    // Require authentication
    const authResult = await requireAuth(request, 'user');
    if ('response' in authResult) {
      return authResult.response;
    }

    // Validate endpoint ID parameter
    const endpointIdValidation = validateEndpointId(endpointId);
    if (!endpointIdValidation.valid) {
      return NextResponse.json(
        {
          error: 'Invalid endpoint ID',
          code: 'VALIDATION_ERROR',
          details: endpointIdValidation.errors,
        },
        { status: 400 }
      );
    }

    const sanitizedEndpointId = endpointIdValidation.sanitized || endpointId;
    const deleted = await deleteEndpoint(endpointId);

    if (!deleted) {
      return NextResponse.json(
        {
          error: 'Endpoint not found',
          code: 'NOT_FOUND',
        },
        { status: 404 }
      );
    }

    return NextResponse.json({
      success: true,
      message: 'Endpoint deleted',
    });
  } catch (error) {
    console.error('[endpoints] Delete error:', error);
    return NextResponse.json(
      {
        error: 'Failed to delete endpoint',
        code: 'INTERNAL_ERROR',
        details: error instanceof Error ? error.message : 'Unknown error',
      },
      { status: 500 }
    );
  }
}

