// =============================================================================
// ENDPOINT SCHEMAS
// =============================================================================
// Zod schemas for endpoint creation and updates

import { z } from 'zod';

/**
 * Schema for creating a new metered endpoint
 */
export const createEndpointSchema = z.object({
  name: z.string()
    .min(1, 'Name is required')
    .max(100, 'Name must be less than 100 characters')
    .regex(/^[a-zA-Z0-9\s_-]+$/, 'Name can only contain letters, numbers, spaces, hyphens, and underscores'),
  
  upstreamUrl: z.string()
    .url('Must be a valid URL')
    .max(2048, 'URL must be less than 2048 characters')
    .refine((url) => {
      // Ensure URL uses http or https
      return url.startsWith('http://') || url.startsWith('https://');
    }, 'URL must use http:// or https://'),
  
  price: z.string()
    .regex(/^\d+$/, 'Price must be a positive integer in smallest units')
    .refine((val) => {
      const num = BigInt(val);
      const zero = BigInt(0);
      const max = BigInt('1000000000000000000');
      return num > zero && num <= max; // Max 1M tokens
    }, 'Price must be between 1 and 1000000000000000000'),
  
  network: z.string()
    .min(1, 'Network is required')
    .max(50, 'Network must be less than 50 characters')
    .refine((val) => {
      // Accept legacy network names or CAIP-2 identifiers
      return val.includes(':') ? /^[a-z0-9]+:[a-z0-9]+$/i.test(val) : true;
    }, 'Network must be a legacy name (e.g., "base") or CAIP-2 identifier (e.g., "eip155:8453")'),
  
  asset: z.string()
    .regex(/^0x[a-fA-F0-9]{40}$/, 'Asset must be a valid Ethereum address')
    .or(z.string().regex(/^[a-zA-Z0-9_-]+$/, 'Asset must be alphanumeric'))
    .or(z.string().regex(/^[a-z0-9]+:[a-z0-9]+\/[a-z0-9]+:[a-z0-9]+$/i, 'Asset must be a CAIP-19 identifier (e.g., "eip155:8453/erc20:0x...")')),
  
  description: z.string()
    .max(500, 'Description must be less than 500 characters')
    .optional(),
  
  metadata: z.record(z.any()).optional(),
}).strict();

/**
 * Schema for updating an endpoint
 */
export const updateEndpointSchema = z.object({
  name: z.string()
    .min(1, 'Name is required')
    .max(100, 'Name must be less than 100 characters')
    .regex(/^[a-zA-Z0-9\s_-]+$/, 'Name can only contain letters, numbers, spaces, hyphens, and underscores')
    .optional(),
  
  upstreamUrl: z.string()
    .url('Must be a valid URL')
    .max(2048, 'URL must be less than 2048 characters')
    .refine((url) => {
      return url.startsWith('http://') || url.startsWith('https://');
    }, 'URL must use http:// or https://')
    .optional(),
  
  price: z.string()
    .regex(/^\d+$/, 'Price must be a positive integer in smallest units')
    .refine((val) => {
      const num = BigInt(val);
      const zero = BigInt(0);
      const max = BigInt('1000000000000000000');
      return num > zero && num <= max;
    }, 'Price must be between 1 and 1000000000000000000')
    .optional(),
  
  description: z.string()
    .max(500, 'Description must be less than 500 characters')
    .optional(),
  
  metadata: z.record(z.any()).optional(),
  
  enabled: z.boolean().optional(),
}).strict().refine(
  (data) => Object.keys(data).length > 0,
  'At least one field must be provided for update'
);

/**
 * Schema for endpoint ID parameter
 */
export const endpointIdSchema = z.string()
  .regex(/^ep_\d+_[a-z0-9]+$/, 'Invalid endpoint ID format')
  .max(100, 'Endpoint ID too long');

/**
 * Type exports
 */
export type CreateEndpointInput = z.infer<typeof createEndpointSchema>;
export type UpdateEndpointInput = z.infer<typeof updateEndpointSchema>;
export type EndpointId = z.infer<typeof endpointIdSchema>;

